.contact {
  background-color: #111;
  color: white;
  width: 100%;
}

.contact-content {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 120px;
}

.contact-content h1 {
  margin: 0;
  padding: 0;
  font-size: 3.5em;
}

.contact-content p {
  margin: 20px 0 0;
  padding: 0;
  font-size: 1.2em;
}

.social-networks {
  display: flex;
  margin-top: 56px;
  font-size: 2.5em;
}

.github {
  color: white;
  margin: 0 16px;
}


.linkedin {
  color: white;
  margin: 0 16px;
}

.instagram {
  color: white;
  margin: 0 16px;
}

.whatsapp {
  color: white;
  margin: 0 16px;
}

.github:hover {
  color: aquamarine;
}

.linkedin:hover {
  color: aquamarine;
}

.instagram:hover {
  color: aquamarine;
}

.whatsapp:hover {
  color: aquamarine;
}

.contact-form {
  display: flex;
  flex-direction: column;
}

.name {
  align-items: center;
  background-color: transparent;
  border: 1px solid aquamarine;
  border-radius: 8px;
  color: white;
  font-size: 20px;
  font-weight: 600;
  height: 60px;
  margin-bottom: 16px;
  padding-left: 16px;
  width: 300px;
}

.email {
  align-items: center;
  background-color: transparent;
  border: 1px solid aquamarine;
  border-radius: 8px;
  color: white;
  font-size: 20px;
  font-weight: 600;
  height: 60px;
  margin-bottom: 16px;
  padding-left: 16px;
  width: 300px;
}

.subject {
  align-items: center;
  background-color: transparent;
  border: 1px solid aquamarine;
  border-radius: 8px;
  color: white;
  font-size: 20px;
  font-weight: 600;
  height: 60px;
  margin-bottom: 16px;
  padding-left: 16px;
  width: 300px;
}

.message {
  align-items: center;
  background-color: transparent;
  border: 1px solid aquamarine;
  border-radius: 8px;
  color: white;
  font-size: 20px;
  font-weight: 600;
  height: 100px;
  margin-bottom: 16px;
  padding: 6px 16px;
  width: 300px;
}

.submit {
  background-color: aquamarine;
  border: none;
  border-radius: 8px;
  color: #21717A;
  cursor: pointer;
  font-size: 2em;
  font-weight: 600;
  height: 60px;
  margin-bottom: 16px;
  transition: all ease-in-out 0.2s;
  width: 300px;
}

.submit:hover {
  background-color: #21717A;
  color: white;
}

@media screen and (min-width: 1200px) {

    .name {
    width: 500px;
  }
  
  .email {
    width: 500px;
  }
  
  .subject {
    width: 500px;
  }
  
  .message {
    width: 500px;
  }
  
  .submit {
    width: 500px;
  }  
}
